<template>
    <div>
        <!-- justify contentsを中央揃えに -->
        <v-row justify="center">
            <v-col cols="12" sm="12">
                <v-sheet min-height="70vh" rounded="lg" class="sheet" id="scrolling-techniques-3">
                    <!-- ここに書く -->
                    <!-- <div>検索：<input type="text" v-model="search" /></div> -->
                    <div>
                        <h3>{{ dayOfWeek }} {{ now }}</h3>
                        <h2>Next:{{nextLecture}}({{nextRoom}})</h2>
                    </div>
                    <div ref="map" id="mapArea" style="height: 400px;"></div>
                </v-sheet>
            </v-col>
        </v-row>
    </div>
</template>




<script>
import jsonTimetable from "@/assets/json/timetable.json"
// import jsonTimetable from "@/assets/json/sample.json"

export default {
    name: 'SchoolMaps',

    data: () => ({
        latitude: 35.18360947059331, // 緯度
        longitude: 137.11300434536787, // 経度
        nextLecture: 'プロジェクト演習', // 次の講義名
        nextRoom: '11-501', // 次の講義室
        TimeTables: jsonTimetable, // json(時間割)
        now: '00:00:00', // 現在時刻
        week: 0, // 今日の曜日(数値)
        dayOfWeek: '日', // 今日の曜日(文字)
        selected: '愛工大', // 選択されてるやつ
    }),

    created() { // 最初に1回実行
        this.getDate();
        setInterval(this.getDate, 1000);
    },

    watch: { // 変数が変わったら実行
        longitude() {
            this.showMap()
        },
        now() {
            if(this.week <= 4) { // 土日以外
                this.getNext();
            } else {
                this.default();
            }
                
        },
        nextRoom() {
            const BuildingNumber = this.nextRoom.substr(0,2);
            console.log(BuildingNumber);
            if (BuildingNumber == '') {
                this.latitude = 35.18360947059331, 
                this.longitude = 137.11300434536787
            } else if (BuildingNumber == '1-') { // 1号館
                this.latitude = 35.18393945476342, 
                this.longitude = 137.11122595608316
            } else if (BuildingNumber == '1号館別館') { // 1号館別館
                this.latitude = 35.184755757058426,
                this.longitude = 137.1109978157656
            } else if (BuildingNumber == '新2号館') { // 新2号館
                this.latitude = 35.183703880504815, 
                this.longitude = 137.11419974761313
            } else if (BuildingNumber == 'C1') { // 3号館
                this.latitude = 35.183922211024395,
                this.longitude = 137.11504557393604
            } else if (BuildingNumber == 'C2') { // 3号館別館
                this.latitude = 35.18370320371449,
                this.longitude = 137.11509877225797
            } else if (BuildingNumber == 'K4') { // 4号館
                this.latitude = 35.18482994471485, 
                this.longitude = 137.1150659575287
            }  else if (BuildingNumber == '4号館別館') { // 4号館別館
                this.latitude = 35.18519724115331,
                this.longitude = 137.11532291082867
            }else if (BuildingNumber == 'ER') { // 5号館
                this.latitude = 35.18458493374964,
                this.longitude = 137.1140735165559
            } else if (BuildingNumber == '6号館') { // 6号館
                this.latitude = 35.18473046120468,
                this.longitude = 137.11610454506928 
            } else if (BuildingNumber == '7-') { // 7号館
                this.latitude = 35.1846817556055,
                this.longitude = 137.11249795736597
            } else if (BuildingNumber == '8-') { // 8号館
                this.latitude = 35.18420802854306,
                this.longitude = 137.11458722046635
            } else if (BuildingNumber == 'G3') { // 9号館
                this.latitude = 35.18475873791302, 
                this.longitude = 137.11156065100775
            } else if (BuildingNumber == 'G2') { // 10号館
                this.latitude = 35.18419280914104, 
                this.longitude = 137.110973746277
            } else if (BuildingNumber == '11') { // 11号館
                this.latitude = 35.185232783157495, 
                this.longitude = 137.11593943875485
            } else if (BuildingNumber == '12') { // 12号館
                this.latitude = 35.18372755469411,
                this.longitude = 137.1134808811515
            } else if (BuildingNumber == '13') { // 13号館
                this.latitude = 35.184737832991345, 
                this.longitude = 137.1157716465662
            }
        },
    },

    methods: {

        getDate() { // 現在時刻 & 今日の曜日取得
            const weekItems = ["月", "火", "水", "木", "金", "土","日"];
            this.date = new Date();
            this.week = this.date.getDay() - 1;
            if(this.week == -1) {
                this.week = 6;
            }
            this.dayOfWeek = weekItems[this.week];  
            this.now = this.date.getHours() + ":" + this.date.getMinutes() + ":" + this.date.getSeconds();
        },

        default() { // 土日などの時
            this.nextLecture = '';
            this.nextRoom = '';
        },

        getNext() { // 現在時刻から次の講義を検索
            const hoursOfLecture = [9,10,13,14,16,18]; // 講義の時間(時)
            const minutesOfLecture = [0,40,0,40,20,0]; // 講義の時間(分)
            try{ // classes[].periodがない場合を無理やりtry catchした.....
                const toDayLectures = this.TimeTables[this.week]; // 今日の講義
                let minHourDis = 100000; // min hour distance
                let minMinuDis = 100000; // min minute distance
                let min = 0; // 最小distanceのindex
                for(let i = 0; i < Object.keys(toDayLectures).length; i++) {
                    // console.log(toDayLectures.classes[i].period + toDayLectures.classes[i].name); // Debug用
                    const period = toDayLectures.classes[i].period; // n限
                    const hourDis = Math.abs(hoursOfLecture[period - 1] - Number(this.date.getHours()));
                    const minuDis = Math.abs(minutesOfLecture[period - 1] - Number(this.date.getMinutes()));

                    if(minHourDis >= hourDis && minMinuDis > minuDis) {
                        minHourDis = hourDis;
                        minMinuDis = minuDis;
                        min = i;
                    }
                }

                if(this.nextLecture != toDayLectures.classes[min].name) { // 変化があったら
                    this.nextLecture = toDayLectures.classes[min].name;
                    this.nextRoom = toDayLectures.classes[min].locate;
                }
            }catch(e){
                this.default();
            }
        }, 

        showMap() { // 地図を表示
            // APIキーを使用しスクリプト呼び出すやつ
            const script = document.createElement('script');
            script.src = 'https://maps.googleapis.com/maps/api/js?key=AIzaSyD31Xh6RcYFzbDYNl05jZ9gp7ZZ9BZWni0&callback=initMap' 
            + "&libraries=places";
            script.async = true;
            document.head.appendChild(script);

            // スクリプト呼び出し後のコールバック
            window.initMap = () => {
                const myLatLng = { lat: Number(this.latitude), lng: Number(this.longitude) };
                const map = new window.google.maps.Map(this.$refs.map, {
                center: myLatLng,
                zoom: 18,
                });
                new window.google.maps.Marker({ 
                    position: myLatLng, 
                    map 
                });
            };
        },

        getLocation () { // 現在地を取得
            if (!navigator.geolocation) {
                alert('現在地情報を取得できませんでした。お使いのブラウザでは現在地情報を利用できない可能性があります。エリアを入力してください。')
                return
            }
            const options = {
                enableHighAccuracy: false,
                timeout: 5000,
                maximumAge: 0
            }
            navigator.geolocation.getCurrentPosition(this.success, this.error, options)
        },

        success (position) { // 現在地取得できたら変更
            // console.log("lng " + position.coords.latitude);
            // console.log("lat " + position.coords.longitude);
            // console.log("ロケーションを取得しました")

            this.latitude = position.coords.latitude;
            this.longitude = position.coords.longitude;
        },

        error (error) { // 現在地取得できなかったらエラー
            switch (error.code) {
                case 1: //PERMISSION_DENIED
                    alert('位置情報の利用が許可されていません')
                    break
                case 2: //POSITION_UNAVAILABLE
                    alert('現在位置が取得できませんでした')
                    break
                case 3: //TIMEOUT
                    alert('タイムアウトになりました')
                    break
                default:
                    alert('現在位置が取得できませんでした')
                    break
            }
        },
    }
}
</script>
