<template>
    <div>
        <!-- justify contentsを中央揃えに -->
        <v-row justify="center">
            <v-col cols="12" sm="12">
                <v-sheet min-height="70vh" rounded="lg" id="scrolling-techniques-3">
                    <!-- ここに書く -->
                    <!-- ゲストのとき -->
                    <v-container v-if="AikodaiId == 'guest'">
                        <v-card class="mx-auto" max-width="500" :height="getCardHeight">
                            <v-card-title class="text-h6 font-weight-regular justify-space-between">
                                <span>時間割にアクセス出来ません！！</span>
                            </v-card-title>
                            <v-card-text>
                                <div class="pa-4 text-center">
                                    <h3 class="text-h6 font-weight-light mb-2">
                                        ゲストユーザーは時間割にアクセス出来ません。
                                        アクセスするにはログインしてください。
                                    </h3>
                                    <span class="text-caption text-grey">ログインは下のボタンから行うことができます</span>
                                </div>
                            </v-card-text>
                            <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn color="blue-darken-1" variant="flat" @click="toLoginPage">
                                    今すぐログインを行う
                                </v-btn>
                                <v-spacer></v-spacer>
                            </v-card-actions>
                        </v-card>
                    </v-container>
                    <!-- ログイン出来ているとき -->
                    <v-container v-else>
                        <v-row no-gutters>
                            <v-spacer></v-spacer>
                            <v-col v-for="Day in DayOfWeek" :key="Day.id" cols="1">
                                <!-- <v-sheet class="pa-2 ma-2">
                                    {{ Day.title }}
                                </v-sheet> -->
                                <v-chip variant="outlined">
                                    {{ Day.title }}
                                </v-chip>
                            </v-col>
                            <v-spacer></v-spacer>
                        </v-row>
                        <v-row no-gutters v-for="period in 7" :key="period">
                            <v-col>
                                <v-sheet class="pa-2 ma-2">
                                    {{ period }}限
                                </v-sheet>
                            </v-col>
                            <v-col v-for="TimeTable in TimeTables" :key="TimeTable.youbi">
                                <v-sheet class="pa-2 ma-2" style="font-size: 0.7rem;">
                                    <div v-for="Class in TimeTable.classes" :key="Class.period">
                                        <div v-if="Class.period == period">
                                            {{ Class.name }}
                                            {{ Class.teacher }}
                                            {{ Class.locate }}
                                        </div>
                                    </div>
                                </v-sheet>
                            </v-col>
                        </v-row>
                    </v-container>
                </v-sheet>
            </v-col>
        </v-row>
    </div>
</template>
<script>
import { getCurrentInstance } from 'vue';

const usevuetify = () => {
    const instance = getCurrentInstance()
    if (!instance) {
        throw new Error(`vuetify instance doesn't exist`)
    }
    return instance.proxy.$vuetify
}

//時間割のjsonファイルを読み込む
//あとでAPIから取得するようにするため、URLを記述する
// import jsonTimetable from `https://api.aitplus.hyouhyan.com/timetable/?id=${LoginPage.AikodaiId}&pass=${LoginPage.PassWord}`
import jsonTimetable from "@/assets/json/timetable.json"

export default {

    name: 'TimeTable',
    data: () => ({
        AikodaiId: 0,
        PassWord: 0,
        DayOfWeek: [
            // { id: 0, title: '' },
            { id: 1, title: '月' },
            { id: 2, title: '火' },
            { id: 3, title: '水' },
            { id: 4, title: '木' },
            { id: 5, title: '金' },
            { id: 6, title: '土' },
            { id: 7, title: '日' },
        ],
        TimeTables: jsonTimetable,
    }),
    props: {
        // ログインユーザーの情報を受け取る
        aikodaiId: {
            type: String,
            default: '',
        },
        password: {
            type: String,
            default: '',
        },
    },
    created: function () {
        this.AikodaiId = this.aikodaiId;
        this.PassWord = this.password;
    },
    methods: {
        toLoginPage: function () {
            this.$emit('to-login-page');
        },

    },
    computed: {
        getCardHeight() {
            const vuetify = usevuetify();
            return vuetify.display.xs ? '450' : '250';
        },
    }
}
</script>
